'use client';

import React from 'react';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const VentaResumen = () => {
  const { ventas } = useVentas();

  const totalVentas = ventas.length;
  const totalIngresos = ventas.reduce((acc, v) => acc + v.costoTotal, 0);
  const totalDescuentos = ventas.reduce((acc, v) => acc + v.descuento, 0);

  return (
    <section className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-5">
      <ResumenCard
        icon="üßæ"
        label="Total de Ventas"
        value={totalVentas.toString()}
        color="text-indigo-600"
        bg="bg-indigo-100"
      />
      <ResumenCard
        icon="üí∞"
        label="Ingresos Totales"
        value={formatCurrency(totalIngresos)}
        color="text-green-600"
        bg="bg-green-100"
      />
      <ResumenCard
        icon="üìâ"
        label="Descuentos Aplicados"
        value={formatCurrency(totalDescuentos)}
        color="text-red-500"
        bg="bg-red-100"
      />
    </section>
  );
};

type CardProps = {
  icon: string;
  label: string;
  value: string;
  color: string;
  bg: string;
};

const ResumenCard = ({ icon, label, value, color, bg }: CardProps) => (
  <div className={`flex items-start gap-4 rounded-xl border border-gray-200 p-5 shadow-sm ${bg}`}>
    <div className="text-3xl">{icon}</div>
    <div>
      <p className="text-sm text-gray-600 font-medium">{label}</p>
      <p className={`text-xl font-bold mt-1 ${color}`}>{value}</p>
    </div>
  </div>
);

// ‚úÖ Esta l√≠nea evita el error que tuviste
export default VentaResumen;
